body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar{
    background-color: #171b20;
    display: flex;
    justify-content: center;
}
.navbar a{
    color: white;
    padding: 1.5rem 1.7rem;
    text-decoration: none;
    font-size: 1rem;
    font-family: 'Amatic SC',
    cursive;
}

.navbar a:hover{
    background-color: #ace0f9;
    color: black;
}
.App{
    background-image: linear-gradient(to top, #30cfd0 0%, #a561f8 100%);
}
.big-title{
   
    text-align: center;
     font-family: 'Amatic SC',
     cursive;
     font-size: 2.2rem;
     position: relative;
     
}
.card-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}
.cards{
    background-color: black;
    padding: 1.4rem;
    border-radius: .6rem;
    box-shadow: 3px 3px 5px rgba( 0 ,0,0,.7);
    margin: 1rem;
    position: relative;
    overflow: hidden;
}
.title{
    font-family: 'Amatic SC',
    cursive;
    font-size: 1rem;
    padding: 1rem;
    color: white;
}

.title h2{
    margin: 0;
}
.cards img{
    object-fit: cover;
    width: 400px;
    height: 350px;
    border-radius: 7px;
    -webkit-filter: grayscale(0%);
            filter: grayscale(0%);
}
.cards img:hover{
    transition: -webkit-filter .7s ease-in-out;
    transition: filter .7s ease-in-out;
    transition: filter .7s ease-in-out, -webkit-filter .7s ease-in-out;
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
}

.card-over{
    position: absolute;
    background-color: rgba(0,0,0,.5);
    color: white;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 1rem;
    transform: translateY(130%);
    transition: transform .7s ease-in-out;
    overflow: auto;
    max-height: 100%;
   
}

.cards:hover .card-over{
    transform: translateY(0%);
}
